  background-image: url(Assets/snow.jpg); }

.rain {
  background-image: url(Assets/shower_rain.jpg); }

.scattered {
  background-image: url(Assets/scattered_clouds.jpeg); }

.mist {
  background-image: url(Assets/fog.jpeg); }

.few {
  background-image: url(Assets/few_clouds.jpeg); }

.broken {
  background-image: url(Assets/broken_clouds.jpeg); }

.dark {
  background-color: #000; }



